@keyframes content_blocks {
	from {
	}
	to {
		height: 500px;
	}
}

@keyframes year {
	from {
		top: 5px;
	}
	to {
		top: -15px;
		color: var(--text-color);
	}
}

@keyframes texts {
	from {
	}
	to {
		color: var(--text-color);
	}
}

@keyframes img {
	from {
	}
	to {
		background-color: rgba(0, 0, 0, 0);
	}
}

.main {
	margin-top: 100px;
}

.country_block,
.country_block_reversed {
	width: 100vw;
	position: relative;
	display: flex;
	flex-direction: column;
	.year {
		height: 0px;
		text-align: center;
		background-color: var(--background-color);
		h2 {
			display: inline;
			border-radius: 10px;
			padding: 2px 10px;
			position: relative;
			color: rgba(0, 0, 0, 0);
		}
	}

	.layout {
		display: flex;
		justify-content: space-between;
		.text_info {
			width: 54.2vw;
			margin: 0 20px;
			color: rgba(0, 0, 0, 0);
			h2 {
				position: relative;
				top: 10px;
			}
			p {
				position: relative;
				top: 15px;
			}
		}

		.image_container {
			height: 500px;
			width: 45vw;
			position: relative;
			overflow: hidden;
			img {
				position: relative;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			div {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}

	&.animate {
		.year {
			h2 {
				animation: year 0.5s linear forwards;
			}
		}
		.text_info {
			h2 {
				animation: texts 2s linear forwards;
			}
			p {
				animation: texts 2s linear forwards;
			}
		}
		.image_container > div {
			animation: img 0.5s linear forwards;
		}
	}
}

