	background-color: var(--background-color);
	.year {
		h2 {
			background-color: var(--background-color);
		}
	}

	.layout {
		.text_info {
			h2 {
			}
			p {
			}
		}

		.image_container {
			img {
			}
			div {
				background-color: var(--background-color);
			}
		}
	}
}

.country_block_reversed {
	background-color: var(--background-color-1);
	.year {
		h2 {
			background-color: var(--background-color-1);
		}
	}

	.layout {
		flex-direction: row-reverse;
		.text_info {
			h2 {
			}
			p {
			}
		}

		.image_container {
			img {
			}
			div {
				background-color: var(--background-color-1);
			}
		}
	}

	&.animate {
	}
}

