	.country_block,
	.country_block_reversed {
		.layout {
			flex-direction: column;
			align-items: center;
			.text_info {
				width: 90vw;
				h2 {
					text-align: center;
				}
				p {
				}
			}

			.image_container {
				width: 100vw;
				img {
				}
			}
		}
	}
}

